import React, { Component } from 'react';

class Nav extends Component {
  state = {
    navItems: ['factory', 'recycle', 'distribution','users'],

  };

  render() {
    return ( 
      <nav className='bar'>
        <ul>
          {this.state.navItems.map((item) => (
            <li key={item.length}>
              <a href='#'>
                <div>
                  <img src={'./image/' + item + '.png'} alt=''/>
                  <p>{item}</p>
                </div>
              </a>
            </li>
          ))}
        </ul>
      </nav>
      // <nav className="bar">
      //   <ul>
      //     <li><a href='#'>xxxx</a></li>
      //     <li><a href='#'>xxxx</a></li>
      //     <li><a href='#'>xxxx</a></li>
      //     <li><a href='#'>xxxx</a></li>
      //   </ul>
      // </nav>
    );
  }
}

export default Nav;
