import React, { Component } from "react";

class Trash extends Component {
  state = {
    divStyle: {
      position: "relative",
      float: "left",
      textAlign: "center",
      width: "50%",
    },
    imgStyle: {
      position: "relative",
      float: "right",
    },
  };
  
  render() {
    const { trash } = this.props;
    return (
      <button className="trash">
        <img
          src={"./image/" + trash[0] + ".png"}
          width="50px"
          height="50px"
          alt=""
          style={this.state.imgStyle}
        />
        <div style={this.state.divStyle}>{trash[1]}</div>
      </button>
    );
  }
}

export default Trash;
