import React, { Component } from 'react';

class Bin extends Component {
  render() {
    const {onClick} = this.props;
    return (
      <button id='bin' onClick={()=> onClick(this.props.bin.id)}>
        {this.props.bin.name}
        <img
          src='./image/bin.png'
          width='30px'
          height='30px'
          alt=''
          style={{ float: 'right', position: 'relative' }}
        />
        <p style={{ fontSize: '7px' }}>{this.props.bin.location}</p>
      </button>
    );
  }
}

export default Bin;
